import type { ProfileDetail, ProfileParamas } from '@/types/member'
import { http } from '@/utils/http'
//http包含了拦截器
export const getMemberProfileAPI = () => {
  return http<ProfileDetail>({
    method: 'GET',
    url: '/member/profile',
  })
}
export const PutMeberProfile = (data: ProfileParamas) => {
  return http<ProfileDetail>({
    method: 'PUT',
    url: '/member/profile',
    data,
  })
}
